<script>
import Swiper from 'swiper';
import axios from 'axios';
export default {
    name: 'discover',
    data() {
        return {
            liNum: 1,
            bgColor: '#8F9F9E',
            rList: [],
            perList: [],
            newList1: [],
            newList2: [],
            hotSong: [],
            newSong: [],
            nativeSong: [],
            rzSinger: []
        }
    },
    mounted() {
        this.swiperF();
        this.swiperS();
        this.recommendList();
        this.album();
        this.getArtist();
        this.songList(19723756, 1);
        this.songList(3779629, 2);
        this.songList(2884035, 3);
    },
    methods: {
        swiperF() {
            let vm = this;
            var swiper = new Swiper('.swiper-container', {
                effect: 'fade',
                loop: true,
                simulateTouch: false,//
                speed: 1000,
                autoplay: {
                    delay: 5000//1
                },
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                },
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev',
                },
                on: {
                    transitionStart: function () {
                        if (this.activeIndex === 1) {
                            vm.bgColor = '#8F9F9E';
                        } else if (this.activeIndex === 2) {
                            vm.bgColor = '#b3ecff';
                        } else if (this.activeIndex === 3) {
                            vm.bgColor = '#0F1012';
                        } else if (this.activeIndex === 4) {
                            vm.bgColor = '#0F1012';
                        } else if (this.activeIndex === 5) {
                            vm.bgColor = '#E4E9D3';
                        } else if (this.activeIndex === 6) {
                            vm.bgColor = '#0F190E';
                        } else if (this.activeIndex === 7) {
                            vm.bgColor = '#D80E00';
                        } else if (this.activeIndex === 8) {
                            vm.bgColor = '#000';
                        } else if (this.activeIndex === 9) {
                            vm.bgColor = '#8F9F9E';
                        }
                    },
                }
            });
        },
        swiperS() {
            let vm = this;
            var swiper = new Swiper('.swiper-container2', {
                // loop: true,
                simulateTouch: false,//
                speed: 1000,
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev',
                }
            });
        },
        // 
        recommendList() {
            let vm = this;
            axios.get('http://musicapi.leanapp.cn/personalized').then(function (res) {
                let arr = res.data.result;
                vm.rList = arr.slice(0, 8);
                vm.perList = arr.slice(8, 11);
            }).catch(function (error) {
                console.log(error);
            });
        },
        // 
        album() {
            let vm = this;
            axios.get('http://musicapi.leanapp.cn/top/album', {
                params: {
                    offset: 0,
                    limit: 10
                }
            }).then(function (res) {
                let arr = res.data.albums;
                vm.newList1 = arr.slice(0, 5);
                vm.newList2 = arr.slice(5, 10);
            }).catch(function (error) {
                console.log(error);
            });
        },
        songList(id, type) {
            let vm = this;
            axios.get('https://api.bzqll.com/music/netease/songList', {
                params: {
                    key: 579621905,
                    id: id,
                    limit: 10,
                    offset: 0
                }
            }).then(function (res) {
                let data = res.data.data.songs;
                if (type === 1) {
                    vm.hotSong = data.slice(0, 10);
                } else if (type === 2) {
                    vm.newSong = data.slice(0, 10);
                } else if (type === 3) {
                    vm.nativeSong = data.slice(0, 10);
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        getArtist() {
            let vm = this;
            axios.get('http://musicapi.leanapp.cn/artist/list', {
                params: {
                    cat: 5001,
                    limit: 5
                }
            }).then(function (res) {
                vm.rzSinger = res.data.artists
            }).catch(function (error) {
                console.log(error);
            });
        }
    }
}
</script>

<style lang="less" scoped>
@import "swiper/dist/css/swiper.min.css";
@import "./recommend.less";
</style>




